@font-face {
  font-weight: 100;
  font-family: "open_sans";
  font-style: normal;
  font-display: swap;
  src: url("/fonts/opensanslight.woff2") format("woff2"), url("/fonts/opensanslight.woff") format("woff"); }

@font-face {
  font-weight: 300;
  font-family: "open_sans";
  font-style: normal;
  font-display: swap;
  src: url("/fonts/opensans.woff2") format("woff2"), url("/fonts/opensans.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "open_sans";
  font-style: normal;
  font-display: swap;
  src: url("/fonts/opensansbold.woff2") format("woff2"), url("/fonts/opensansbold.woff") format("woff"); }

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  min-width: 320px;
  font-weight: 300;
  font-size: 18px;
  line-height: 24px;
  font-family: "open_sans", "Arial", sans-serif;
  color: #444444;
  letter-spacing: normal;
  background-color: white; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0); }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  width: 100%;
  padding: 21px; }

@media (min-width: 768px) {
  .container {
    margin: 0 auto;
    width: 768px; } }

@media (min-width: 1150px) {
  .container {
    width: 1150px; } }

.section__title {
  display: block;
  max-width: 205px;
  margin: 0 auto;
  font-weight: 100;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
  color: #283645; }

.button {
  border: none;
  background: none;
  text-align: center; }
  .button--transparent {
    width: 100%;
    display: block;
    border: solid 3px #ffffff;
    padding: 20px 40px 17px 40px;
    font-size: 18px;
    font-weight: 700;
    line-height: 18px;
    border-radius: 40px; }

.slider__toggles {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 56px;
  max-width: 60px;
  margin: 0 auto; }

.slider__toggle {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

.slider__label {
  box-sizing: content-box;
  width: 6px;
  height: 6px;
  background: rgba(40, 54, 69, 0.3);
  border-radius: 50%;
  display: block; }

input#rate-1:checked:focus ~ div label:nth-of-type(1) {
  background: transparent;
  border: solid 3px #283645; }

input#rate-2:checked:focus ~ div label:nth-of-type(2) {
  background: transparent;
  border: solid 3px #283645; }

input#rate-3:checked:focus ~ div label:nth-of-type(3) {
  background: transparent;
  border: solid 3px #283645; }

input#rate-2:checked ~ div .rates__table {
  -webkit-transform: translateX(-33%);
          transform: translateX(-33%); }

input#rate-3:checked ~ div .rates__table {
  -webkit-transform: translateX(-66%);
          transform: translateX(-66%); }

.page-header {
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  color: white;
  text-transform: uppercase; }

.page-header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1px;
  background-color: rgba(40, 54, 69, 0.7);
  position: relative;
  z-index: 10; }

.page-header__logo {
  display: inline-flex; }

.page-header__button {
  width: 50px;
  height: 24px; }

.page-header__button > svg {
  height: 100%; }

.page-header__list {
  display: none;
  margin: 0;
  padding: 0;
  list-style: none; }

.page-header__item {
  margin-bottom: 1px;
  box-shadow: 0 1px 0 0 black, inset 0 1px 0 0 rgba(255, 255, 255, 0.15); }

.page-header__item a {
  display: block;
  width: 100%;
  height: 100%;
  padding: 26px;
  text-align: center; }

.intro {
  margin-top: -66px;
  color: #ffffff;
  background-image: url("/img/iphone-hand-mobile@1x.png"), url("/img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position-y: bottom, auto; }
  .intro--main {
    padding-top: 462px; }

.intro__wrapper {
  width: 100%;
  padding: 22px;
  text-align: center;
  background-color: #1d2631; }

.intro__title {
  display: none; }

.intro__apps {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  max-width: 205px;
  margin: 0 auto;
  padding: 30px 0; }

.intro__link {
  display: inline-flex; }

.intro__app {
  display: inline-flex; }

.intro__note {
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;
  text-align: center; }

.advantages {
  overflow: hidden;
  background: linear-gradient(175deg, transparent 30%, #f2f2f2 30%);
  padding-bottom: 300px;
  position: relative; }
  .advantages::after {
    content: "";
    position: absolute;
    width: 157px;
    height: 320px;
    bottom: -40px;
    left: 50%;
    z-index: 5;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    background: url("/img/iphone-screen-mobile@1x.png"); }

.advantages__title {
  margin-bottom: 100px; }

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center; }

.advantages__item {
  display: block;
  margin: 0 auto; }

.advantages__name {
  color: black; }

.advantages__info {
  width: 252px;
  margin: 0 auto;
  font-size: 14px;
  font-weight: normal; }

.rates__table {
  width: 300%;
  border-collapse: collapse; }

.rates__top {
  width: 276px;
  color: #f2f2f2;
  background-color: #283645;
  border: solid 2px #283645;
  border-right: 2px solid #f2f2f2; }

.rates__cell {
  width: 1/3;
  border: solid 2px #e5e5e5; }
  .rates__cell--column1 {
    display: none; }
